
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'

import Timer from 'assembly/timer/timer'
import Scroll from 'assembly/scroll/scroll'
import { validateNotNull, validateEmail, validatePhone } from 'util/rules'
import localStore from 'util/localStore'

export default {
components: {
XInput,
Group,
XButton,
XHeader,
Selector,
Scroll,
PopupPicker,
Timer
},
data () {
return {
pIsMobileActive: true,
pIsEmailActive: true,
mobileVisible: false,
emailVisible: false,
code: '',
email: '',
mobile: '',
region: [],
provinceList: [],
inited: false
}
},
methods: {
sendCode () {
if (!this.$refs.timer.disabled) { // 如果没在锁定状态
this.$tupHttp(this, 'front/login/send?mobile=' + this.mobile).then(res => {
this.$refs.timer.start()
this.$vux.toast.text('验证码已发送')
})
}
},
sendEmail () {
if (this.inited) {
return
}
if (validateEmail(this, this.email)) {
this.inited = true
this.$tupHttp(this, 'front/user/updateMail?email=' + this.email).then(res => {
this.$vux.toast.text('激活邮件已发送，请至邮箱接受邮件确认激活~')
setTimeout(() => {
this.$router.replace({
path: '/login'
})
}, 1000)
})
}
},
send () {
if (this.inited) {
return
}
let regionText = this.$refs.popupPicker.getNameValues().split(' ')
if (validatePhone(this, this.mobile) && validateNotNull(this, regionText, '省市区') && validateNotNull(this, this.code, '验证码')) {
let data = 'clientId=' + localStore.get('TUP_pUid') + '&province=' + regionText[0] + '&city=' + regionText[1] + '&area=' + regionText[2] + '' +
'&mobile=' + this.mobile + '&code=' + this.code
this.$tupHttp(this, 'front/user/activeAccount?' + data).then(res => {
this.inited = false
if (!this.pIsEmailActive) {
this.mobileVisible = false
this.emailVisible = true
} else {
setTimeout(() => {
this.$router.replace({
path: '/login'
})
}, 1000)
}
}, () => {
this.inited = false
}).catch(() => {
this.inited = false
})
}
}
},
created () {
this.pIsMobileActive = this.$route.query.pIsMobileActive === '1' || this.$route.query.pIsMobileActive === 1
this.pIsEmailActive = this.$route.query.pIsEmailActive === '1' || this.$route.query.pIsEmailActive === 1
this.mobile = this.$route.query.pMobile
this.email = this.$route.query.pEmail
if (!this.pIsMobileActive) {
this.$tupHttp(this, 'https://tup-templates.oss-cn-hangzhou.aliyuncs.com/province.json').then(res => {
this.provinceList = res.data.models
})
this.mobileVisible = true
} else if (!this.pIsEmailActive) {
this.emailVisible = true
}
}
}



